import React from 'react';
import PROJECTS from '../data/projects';

const Project = props => {
    const { title, image, description, link } = props.projectRef;

    return (
        <div style={{ display: 'inline-block', width: 300, margin: 10 }}>
            <h3>{title}</h3>
            <img src={image} alt='profile' style={{ height: 120 }} />
            <p>{description}</p>
            <a href={link}>{link}</a>
        </div>
    )
}

const Projects = () => (
    <div>
        <h2>Highlighted Projects</h2>
        <div>
            {
                PROJECTS.map(proj => (
                    <Project key={proj.id} projectRef={proj} />
                ))
            }
        </div>
    </div>
);


export default Projects;